@font-face {
   font-family: 'Krinkes Decor PERSONAL';
   src: url('./fonts/Krinkes/KrinkesDecorPERSONAL.ttf');
}


* {
   box-sizing: border-box;
}

html,
body {
   margin: 0;
   padding: 0;
   font-family: 'Lato',
   sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
   margin: 0 !important;
}