$Lato: 'Lato',
sans-serif;

@mixin link {
   text-decoration: none;
   margin-left: 10px;
   font-family: $Lato;
   font-weight: 700;
   font-size: 14px;
   color: #393939;
   margin: 0;
}

.header__bar {
   position: fixed;
   top: 0;
   width: 100%;
   height: 38px;
   background-color: #f2f2f2;
   z-index: 20;
}

.header__bar_inner {
   display: flex;
   justify-content: space-between;
   align-items: center;
   width: 100%;
   height: 38px;
}

.header__bar_phone {
   a {
      @include link;

      &:hover {
         text-decoration: underline;
      }
   }
}


.header__bar_link {
   display: flex;
   justify-content: space-between;
   width: 600px;

   a,
   span {
      @include link;
      position: relative;
      display: flex;
      justify-content: center;
      text-transform: uppercase;
      width: 25%;
      cursor: pointer;

      &:hover {
         text-shadow: 1px 0px 0px rgb(163, 163, 163);
         color: rgb(71, 122, 199);
      }

      &::before {
         content: '';
         position: absolute;
         right: 0;
         top: 50%;
         transform: translateY(-50%);
         width: 1px;
         height: 11px;
         background-color: #393939;
         box-shadow: 1px 0px 0px 0px rgb(163, 163, 163);
      }
   }
}

.active {
   color: #f09d66 !important;
}

.header__bar_link {
   a {
      position: relative;
      .badge {
         position: absolute;
         top: -6px;
         right: -2px;
         display: flex;
         justify-content: center;
         align-items: center;
         width: 20px;
         height: 20px;
         font-size: 11px;
         font-weight: 300;
         color: #fff;
         background-color: #f09d66;
         border-radius: 50%;
      }
   }
}